import React, {useState, useCallback} from 'react';
import { Modal, Button, Alert, Icon } from 'antd';
import MyDropzone from './Dropzone';
import request from 'utils/request';
import { isSuccess } from 'utils';
import configs from 'utils/config';
import StandardTable, {StandardTableColumnProps} from 'components/StandardTable';
import XLS from 'assets/xls.svg';
import XLSX from 'assets/xlsx.svg';
import TRASH from 'assets/trash.svg';

type ModalPropsType  = {
  visible: boolean;
  toggleVisible: any;
}

const defaultStatus = { uploaded: false, failNum: 0, successNum: 0, totalNum: 0 };
const content = (
  <>
    <img src={XLS} alt="" style={{width:60}}/>
    <h1 style={{marginTop: 20}}>  <a></a></h1>
    <span>10Mxlsxlsx</span>
  </>
)
const accept = ['.xlsx', 'xls'];
const columns:StandardTableColumnProps[] = [
  {
    title: '',
    dataIndex: 'name',
  },
  {
    title: '',
    dataIndex: 'sexStr',
  },
  {
    title: '',
    dataIndex: 'age',
  },
  {
    title: '',
    dataIndex: 'sn',
  },
  {
    title: '',
    dataIndex: 'college',
  },
  {
    title: '',
    dataIndex: 'grade',
  },
  {
    title: '',
    dataIndex: 'clazz',
  },
]
const DataModal = ({visible, toggleVisible}: ModalPropsType) => {

  const [file, setFile] = useState()
  const [upStatus, setUpStatus] = useState(defaultStatus)

  const saveFile = useCallback((file: File) => {
    let formData = new FormData();
    formData.append('file', file);

    setFile(formData)

  }, [setFile])

  const downloadTemplateFile = async (event: any) => {
    event.preventDefault();
    const res = await request({
      url: '/file/downloadStuModel',
      method:'get'
    })
    if (isSuccess(res)) {
      let aElem = document.createElement('a');
      aElem.href = configs.HOST + '/admin/excel/studentModel.xlsx';
      var evt = document.createEvent("MouseEvents");
      evt.initEvent("click", true, true);
      aElem.dispatchEvent(evt);
    }
  }

  const handleSubmit = async () => {
    const res  = await request({
      url: '/file/importStuModel',
      data: file,
      headers: {
        'Content-Type': 'multipart/form-data'
      }
    })
    if (isSuccess(res)) {
      setUpStatus({
        uploaded: true,
        ...res.data
      })
    }
  }
  const resetModal = () => {
    setFile(undefined)
    setUpStatus(defaultStatus)
  }

  return (
    <Modal
      title=""
      width="60%"
      visible={visible}
      onCancel={toggleVisible}
      centered
      footer={
        <div style={{ alignItems: 'center', justifyContent: 'center', display:'flex' }}>
          {
            upStatus.uploaded ?
              <Button onClick={resetModal} disabled={!file}></Button> :
              <Button type='primary' onClick={handleSubmit} disabled={!file}></Button>
          }

        </div>
      }
    >
      <Alert
        message=""
        description={
          <>
            <p><b>1.</b>Excel</p>
            <p><b>2.</b>100010M</p>
            <p><b>3.</b>xlsxlsx</p>
          </>
        }
        type="info"
      />

      <div style={{marginTop:20, marginBottom: 10}}>
        <a href="" style={{ marginRight: 20 }} onClick={downloadTemplateFile}>
          <Icon type="file" style={{marginRight: 5}}/>Excel
        </a>
        <span>(Excel)</span>
      </div>
      {
        upStatus.uploaded ?
          <>
            <h3>
              <b style={{ color: '#1890ff' }}>{upStatus.totalNum}</b>,
              <b style={{ color: '#1890ff' }}>{upStatus.successNum}</b>,
              <b style={{ color: '#FF4D4F' }}>{upStatus.failNum}</b>
            </h3>
            <StandardTable
              rowKey='key'
              columns={columns}
              data={{
                list: [{name: '123', key:1}],
                pagination: {}
              }}
            />
          </> :
          <MyDropzone
            saveFile={saveFile}
            content={content}
            accept={accept}
          />
      }
    </Modal>
  )
}
DataModal.whyDidYouRender = true

export default React.memo(DataModal)
